.createMemePage3 {
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}

.createMemePage3Content {
  height: calc(100vh - 100px);
  width: 100%;
  position: relative;
  box-sizing: border-box;
}

.createMemeBlock {
  display: grid;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 120px;
  width: 100%;
  grid-template-columns: 1fr 30vw 1fr;
  align-items: center;
}

.picMeme {
  grid-column-start: 2;
  grid-column-end: 3;
  width: 30vw;
  height: 30vw;
  background-repeat: no-repeat;
  background-size: contain;
  border-radius: 10px;
}

.btnSaveWrapper {
  display: flex;
  justify-content: center;
  align-items: center;
}